                  </li>
                  <li>
                    3.在<em>时刻</em>
                    输入框中输入一次停靠车站的时刻，8点06分就输入8.06，每个时刻之间用<em
                      >空格</em
                    >
                    隔开。最后点击<strong>解析</strong>
                  </li>
                </ul>
