              <el-table-column
                v-for="(station, idx) in curVisualTable"
                :key="idx"
                header-align="center"
                align="center"
                :prop="station"
                :label="station"
              >
                <template slot-scope="scope">
                  <i v-if="scope.row[station]" class="el-icon-success"></i>
                  <i v-if="!scope.row[station]" class="el-icon-minus"></i>
                  <!-- <span>{{ scope.row[station] }}</span> -->
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="copyright">
            <el-image
              style="width: 50px; height: 50px"
              :src="require('../assets/logo1356.png')"
