            ></el-image>
            <div>
              Simon工具箱时刻表管理系统，由Up主Simon1356开发，始于2021年1月21日。
            </div>
            <div id="jumper">
              <div style="margin-right:32px">
                点击图标去我的视频主页
              </div>
              <el-image
                style="height:24px;width:100px"
                :src="require('../assets/ytb.png')"
                fit="contain"
                @click="gotoYoutube"
              ></el-image>
              <el-image
                style="height:24px;width:100px;"
                :src="require('../assets/bilibili.png')"
                fit="contain"
                @click="gotoBilibili"
              ></el-image>
              <el-image
                style="height:24px;width:100px"
                :src="require('../assets/xigua.png')"
                fit="contain"
                @click="gotoXigua"
              ></el-image>
            </div>
