<script>
  export default {
    name: "MainPage",
    data() {
      return {
        visualPanelShow: true,
        tempTimeTags: "",
        curVisualTable: [], //最终是只有station name的列表
        visualTable: [
          {
            name: "",
            timeTags: [],
            isSub: false,
            isFull: false,
            type: "",
          },
        ],
        searchVisual: "",
        curTable: [],
        inputVisible: false,
        inputValue: "",
        dialogVisible: false,
        oneTrain: {
          isFull: false,
          isSub: false,
          type: "",
          timeTags: [],
          selectedStations: [],
        },
        num: 3,
        searchName: "",
        config: {
          lineName: "",
          pos: "",
          tableShang: [],
          tableXia: [],
          updateDate: "",
        },
        title: ["上行时刻表", "下行时刻表"],
      };
    },
    props: {
      msg: String,
    },
    methods: {
      gotoBilibili() {
        window.open("https://space.bilibili.com/8962001");
      },
      gotoXigua() {
        window.open("https://www.ixigua.com/home/108872980072/");
      },
      gotoYoutube() {
        window.open("https://www.youtube.com/channel/UCcZ2u5X3Rqxw7BYxwTjHWYw");
      },
      gotoTaobao() {
        window.open(
          "https://item.taobao.com/item.htm?spm=a230r.7195193.1997079397.11.5eb73ccaj9T2uD&id=630870492004&abbucket=1"
        );
      },
      edit(data) {
        console.log("scope内容:", data);
        // let selectedStations = Object.keys(data).filter(
        //   (key) => data[key] === true && key != "isFull" && key != "isSub"
        // );
        let selectedStations = this.curVisualTable.filter(
          (station) => data[station] == true
        );
        console.log("selected stations:", selectedStations);
        this.oneTrain.type = data.type;
        this.oneTrain.selectedStations = selectedStations;
        this.oneTrain.timeTags = data.timeTags;
        this.oneTrain.isSub = data.isSub;
        this.oneTrain.isFull = data.isFull;
        this.dialogVisible = true;
      },
      generatePassInfo() {
        let info = this.oneTrain.type + "：经停：";
        for (let i = 0; i < this.oneTrain.selectedStations.length; i++) {
          const stationText = this.oneTrain.selectedStations[i];
          if (i == this.oneTrain.selectedStations.length - 1) {
            info += stationText + "。";
          } else {
            info += stationText + "、";
          }
        }
        this.oneTrain.type = info;
      },
      goDecode() {
        this.oneTrain.timeTags = this.tempTimeTags.split(" ");
      },
      checkVisual() {
        this.visualTable[0].name = this.searchVisual;
        //确定到底是上行还是下行
        this.curVisualTable = [];
        this.config.tableShang.forEach((station) => {
          let filterRes = station.depart.filter((item) =>
            item.type.includes(this.searchVisual)
          );
          if (filterRes.length > 0) {
            this.curVisualTable = this.config.tableShang;
            this.curTable = this.config.tableShang;
          }
        });
        this.config.tableXia.forEach((station) => {
          let filterRes = station.depart.filter((item) =>
            item.type.includes(this.searchVisual)
          );
          if (filterRes.length > 0) {
            this.curVisualTable = this.config.tableXia;
            this.curTable = this.config.tableXia;
          }
        });
        console.log("table is:", this.curVisualTable);
        this.visualTable[0].timeTags = [];
        this.curVisualTable.forEach((station) => {
          let filterRes = station.depart.filter((item) =>
            item.type.includes(this.searchVisual)
          );
          console.log("filter res in depart of each station:", filterRes);
          if (filterRes.length > 0) {
            this.visualTable[0][station.stationName] = true;
            this.visualTable[0].timeTags.push(filterRes[0].time);
            this.visualTable[0].type = filterRes[0].type;
            this.visualTable[0].isSub = filterRes[0].isSub;
            this.visualTable[0].isFull = filterRes[0].isFull;
          } else {
            this.visualTable[0][station.stationName] = false;
          }
        });
        this.curVisualTable = this.curVisualTable.map(
          (station) => station.stationName
        );
        console.log("visual table:", this.visualTable);
      },
      openDialog(table) {
        this.curTable = table;
        if (this.curTable.length < 1) {
          alert("请先添加车站或者导入");
        } else {
          this.$nextTick(() => {
            this.dialogVisible = true;
          });
        }
      },
      confirmOneTrain() {
        console.log("oneTrain:", this.oneTrain);
        if (
          this.oneTrain.selectedStations.length == this.oneTrain.timeTags.length
        ) {
          this.curTable.forEach((item) => {
            let name = item.stationName;
            let checiIndex = item.depart.findIndex(
              (checi) =>
                checi.type.substring(0, this.num + 1) ==
                this.oneTrain.type.substring(0, this.num + 1)
            );
            if (checiIndex > -1) {
              item.depart.splice(checiIndex, 1);
            }
            let index = this.oneTrain.selectedStations.findIndex(
              (value) => value == name
            );
            if (index > -1) {
              let trainItem = {
                isFull: this.oneTrain.isFull,
                isSub: this.oneTrain.isSub,
                time: this.oneTrain.timeTags[index],
                type: this.oneTrain.type,
              };
              item.depart.splice(item.depart.length, 0, trainItem);
            }
          });
          this.$message({
            message: `新增车次 ${this.oneTrain.type.substring(
              0,
              this.num + 1
            )} 成功！`,
            type: "success",
          });
        } else {
          alert("选择的站点数量与时刻数量长度不一致");
        }
      },
      handleClose(tag) {
        this.oneTrain.timeTags.splice(this.oneTrain.timeTags.indexOf(tag), 1);
      },

      showInput() {
        this.inputVisible = true;
        this.$nextTick(() => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
      },

      handleInputConfirm() {
        let inputValue = this.inputValue;
        if (inputValue) {
          this.oneTrain.timeTags.push(inputValue);
        }
        this.inputVisible = false;
        this.inputValue = "";
      },
      moveUp(table, index) {
        let temp = table[index - 1];
        table.splice(index - 1, 1, table[index]);
        table.splice(index, 1, temp);
      },
      moveDown(table, index) {
        let temp = table[index + 1];
        table.splice(index + 1, 1, table[index]);
        table.splice(index, 1, temp);
      },
      deleteStation(table, index) {
        table.splice(index, 1);
      },
      addStation(table) {
        let blankStation = {
          depart: [],
          stationName: "",
        };
        table.splice(table.length, 0, blankStation);
      },
      moveLeft(depart, index) {
        let temp = depart[index - 1];
        depart.splice(index - 1, 1, depart[index]);
        depart.splice(index, 1, temp);
      },
      moveRight(depart, index) {
        let temp = depart[index + 1];
        depart.splice(index + 1, 1, depart[index]);
        depart.splice(index, 1, temp);
      },
      addBlank(depart) {
        let blankItem = {
          isFull: false,
          isSub: false,
          time: "",
          type: "",
        };
        depart.splice(depart.length, 0, blankItem);
      },
      goSave() {
        const data = JSON.stringify(this.config);
        console.log(this.config);
        const blob = new Blob([data], { type: "text/plain" });
        const e = document.createEvent("MouseEvents"),
          a = document.createElement("a");
        a.download = this.config.lineName + "配置文件.json";
        a.href = window.URL.createObjectURL(blob);
        a.dataset.downloadurl = ["text/json", a.download, a.href].join(":");
        e.initEvent(
          "click",
          true,
          false,
          window,
          0,
          0,
          0,
          0,
          0,
          false,
          false,
          false,
          false,
          0,
          null
        );
        a.dispatchEvent(e);
      },
      remove(depart, index) {
        depart.splice(index, 1);
      },
      add(depart, index) {
        depart.splice(index, 0, JSON.parse(JSON.stringify(depart[index])));
      },
      showDetail() {
        console.log("配置文件为:", this.config);
      },
      goRead() {
        var fileInput = document.getElementById("fileInput");
        const reader = new FileReader();
        let that = this;
        reader.onload = function fileReadCompleted() {
          var config = JSON.parse(reader.result);
          console.log(config);
          that.config = config;
          that.$forceUpdate();
        };
        reader.readAsText(fileInput.files[0]);
      },
    },
  };
</script>

